import React from "react"
import { Image } from "react-bootstrap"
import * as styles from "./logo.module.css"
import { Link } from "gatsby"

const Logo = () => {
	return (
		<div className={ styles.logo }>
			<Link to="/">
				<div className={ styles.adjustInner + " " + styles.addShadow }>
					<Image src="/images/logo-square-white-2.svg" alt="Site logo"/>
				</div>
			</Link>
		</div>
	)
}
export default Logo